/* Global Styles */
* {
  box-sizing: border-box;
}

.body {
  margin: 0;
  font-family: Arial, sans-serif;
  flex: 1;
  overflow-y: auto;
}

/* Home Component Styles */
.home-container {
  display: flex;
  flex-direction: column;
}

.action-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #5283ff;
  color: #ffffff;
  padding: 10px 20px;
}

.company-name {
  font-weight: bold;
}

.action-buttons {
  display: flex;
}

.profile-button,
.logout-button {
  cursor: pointer;
  margin-left: 10px;
  padding: 5px 10px;
  border-radius: 5px;
}

.profile-button:hover,
.logout-button:hover {
  background-color: #555;
}

.content {
  display: flex;
  flex: 1;
  width: 100%;
  overflow: hidden;
}

.navbar-container {
  width: 200px; /* Adjust width as needed */
  background-color: #f2f2f2; /* Background color of the navbar */
  border-radius: 15px; /* Rounded corners */
  overflow: hidden;
}

.navbar {
  padding: 10px;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
}

.navbar ul li {
  margin-bottom: 5px;
}

.navbar ul li a {
  display: block;
  padding: 10px;
  border-radius: 15px; /* Rounded corners for each menu item */
  color: #000; /* Default text color */
  text-decoration: none;
  position: relative; /* Position relative for sliding animation */
  transition: background-color 0.3s ease-out; /* Transition for background color */
}

.navbar ul li a.active {
  background-color: rgb(92, 138, 255); /* Background color for selected menu */
  color: white; /* Text color for selected menu */
}

.navbar ul li a:hover::before {
  width: 100%; /* Ensure hover effect width is 100% */
}

.navbar ul li a.active:hover::before {
  width: 0; /* Ensure hover effect width is 0 for the active item */
}

.navbar ul li a.active:hover {
  background-color: rgb(105, 158, 255); /* Background color for selected menu on hover */
}

.navbar ul li a.active:hover::before {
  transform: translateY(-100%); /* Move the sliding border to the top */
}

/* Responsive Design for Mobile View */
@media (max-width: 400px) {
  .navbar-container {
    width: 50px; /* Adjusted width for icons only */
  }
  .navbar ul li {
    display: flex;
    justify-content: center;
  }
  .navbar ul li a {
    padding: 10px 5px; /* Less padding for icons */
    text-align: center;
  }
  .navbar ul li a span {
    display: none; /* Hide text */
  }
  .navbar ul li a img {
    width: 20px; /* Size of the icon */
    height: 20px;
  }
}
